import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useCertificateNotExistInstitute } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import { useState } from "react";

const CertificateIssueNonExistingMenu = ({ onClose, instituteId }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [certificateNotExistInstitute] = useCertificateNotExistInstitute();
  const [disabled, setDisabled] = useState(false);
  const onNavigate = (type) => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      certificateNotExistInstitute({
        id: instituteId,
      })
        .then((res) => {
          // console.info("res.data?.institute", res.data?.institute);
          if (res.data?.institute) {
            setDisabled((pre) => !pre);

            navigate(`/q/${params.username}/student/certificate/form`, {
              state: {
                instituteId: instituteId,
                is_leaving: type,
                staffId: getQuery.state?.sid,
                whichCertificate: "NOT_EXIST",
                student: {
                  institute: res.data?.institute,
                },
              },
            });
          }
        })
        .catch({});
    }
  };
  return (
    <ModalMenu
      onClose={onClose}
      customStyle={{
        width: "18rem",
      }}
      disabled={disabled}
    >
      <h6 onClick={() => onNavigate("leaving")}>{t("leaving")}</h6>
      <h6 onClick={() => onNavigate("MTC")}>{t("transfer_certificate")}</h6>
      <h6 onClick={() => onNavigate("MLC")}>{t("migration_tc")}</h6>
      <h6 onClick={() => onNavigate("transfer")}>{t("migration_lc")}</h6>
    </ModalMenu>
  );
};

export default CertificateIssueNonExistingMenu;
