import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useCertificateRequestList } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CertificateRequestCard from "./CertificateRequestCard";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import CertificateRequestExport from "./CertificateRequestExport";

const CertificateRequest = ({ instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    certificateRequestList,
    certificateRequestListLoading,
    certificateRequestListRefetch,
  } = useCertificateRequestList({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (certificateRequestList?.list?.length) {
      let obj = nextPage(certificateRequestList?.list?.length);
      setState(() => obj);
    }
  }, [certificateRequestList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !certificateRequestListLoading &&
      certificateRequestList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    certificateRequestListRefetch();
  }, [certificateRequestListRefetch]);

  const onClose = () => {
    setStep("");
  };

  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
        <CreateButton
          label="export"
          onAction={() => setStep(1)}
          customStyle={{
            margin: "0",
            padding: "0.3rem 1rem",
          }}
        />
      </div>

      {certificateRequestList?.list?.map((certificate, index) =>
        certificateRequestList?.list?.length === index + 1 ? (
          <div key={certificate?._id} ref={ref}>
            <CertificateRequestCard
              certificate={certificate}
              instituteId={instituteId}
              onRefetch={onRefetch}
            />
          </div>
        ) : (
          <CertificateRequestCard
            certificate={certificate}
            key={certificate?._id}
            instituteId={instituteId}
            onRefetch={onRefetch}
          />
        )
      )}
      {certificateRequestListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        certificateRequestList?.list?.length === 0 && (
          <EmptyMessage
            title="No item found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        certificateRequestList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No item."
          />
        )
      )}
      {step === 1 ? (
        <CertificateRequestExport onClose={onClose} instituteId={instituteId} />
      ) : null}
    </>
  );
};

export default CertificateRequest;
