import React, { useEffect } from "react";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import { useTranslation } from "react-i18next";
import { useGetCertificateInstituteCharges } from "../../../../../hooks/member_tab/department-api";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";

const CertificateFund = ({ instituteId, issuedCOunt }) => {
  const { t } = useTranslation();
  const {
    getCertificateInstituteCharges,
    getCertificateInstituteChargesRefetch,
  } = useGetCertificateInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (instituteId) {
      getCertificateInstituteChargesRefetch();
    }
  }, [instituteId, getCertificateInstituteChargesRefetch]);

  return (
    <>
      <div className={style.with_search}>
        <h6>{t("certificate_fund")}</h6>
      </div>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginTop: "0.5rem",
        }}
      />

      <div
        className={style.with_search}
        style={{
          marginTop: "1rem",
          justifyContent: "flex-start",
          gap: "1rem",
        }}
      >
        <h6
          style={{
            fontWeight: "400",
            fontSize: "13px",
          }}
        >
          {t("total_certificate_issue")}
        </h6>
        <h6>{issuedCOunt}</h6>
      </div>
      <div
        className={style.with_search}
        style={{
          marginTop: "1rem",
          justifyContent: "flex-start",
          gap: "1rem",
        }}
      >
        <h6
          style={{
            fontWeight: "400",
            fontSize: "13px",
          }}
        >
          {t("online_collection")} {":"}
        </h6>

        <h6>
          {t("rs")}.
          {getCertificateInstituteCharges?.certificate
            ?.certificate_fund_collection?.online ?? 0}
        </h6>
      </div>
      <div
        className={style.with_search}
        style={{
          marginTop: "1rem",
          justifyContent: "flex-start",
          gap: "1rem",
        }}
      >
        <h6
          style={{
            fontWeight: "400",
            fontSize: "13px",
          }}
        >
          {t("offline_collection")} {":"}
        </h6>

        <h6>
          {t("rs")}.
          {getCertificateInstituteCharges?.certificate
            ?.certificate_fund_collection?.offline ?? 0}
        </h6>
      </div>
    </>
  );
};

export default CertificateFund;
