import { useEffect, useState } from "react";
import {
  useCertificateBonafideDublicate,
  useCertificateLeavingFormSetting,
} from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";

import Switch from "@mui/material/Switch";
import FormInput from "../../../Ui/Input/FormInput";
import FormFile from "../../../Ui/Input/FormFile";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../../ImageCrop/CustomCrop";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import StringLength from "../../../../Custom/Validation/StringLength";
import { useEditEditable } from "../../../../hooks/institute/institute-staff-student-api";
const label = { inputProps: { "aria-label": "Switch demo" } };

const CertificateSetting = ({ instituteId, masterData }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    certificate_leaving_form_student: "",
    is_dublicate_bonafide: false,
    insAffiliated: "",
    insEditableText_one: "",
    insEditableText_two: "",
    affliatedLogo: "",
    authority: "",
    authority_signature: "",
    autority_stamp_profile: "",
    leave_certificate_selection: "",
  });

  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeFile, setActiveFile] = useState("");
  const [fileUpload] = useFileUpload();

  const [certificateLeavingFormSetting] = useCertificateLeavingFormSetting();
  const [certificateBonafideDublicate] = useCertificateBonafideDublicate();
  const [editEditable] = useEditEditable();
  useEffect(() => {
    if (masterData) {
      setState((prev) => ({
        ...prev,
        certificate_leaving_form_student:
          masterData?.certificate_leaving_form_student,
        is_dublicate_bonafide:
          masterData?.is_dublicate_bonafide === "Yes" ? true : false,
        insAffiliated: masterData?.insAffiliated ?? "",
        insEditableText_one: masterData?.insEditableText_one ?? "",
        insEditableText_two: masterData?.insEditableText_two ?? "",
        affliatedLogo: masterData?.affliatedLogo ?? "",
        authority: masterData?.authority ?? "",
        authority_signature: masterData?.authority_signature ?? "",
        autority_stamp_profile: masterData?.autority_stamp_profile ?? "",
        leave_certificate_selection:
          masterData?.leave_certificate_selection ?? "",
      }));
    }
  }, [masterData]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onSave = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      certificateLeavingFormSetting({
        id: instituteId,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onDublicateCopyAddOn = (e) => {
    if (instituteId) {
      setState((prev) => ({
        ...prev,
        is_dublicate_bonafide: e.target.checked,
      }));
      setDisabled((pre) => !pre);
      certificateBonafideDublicate({
        id: instituteId,
        bodyParameter: {
          is_dublicate_bonafide: e.target.checked ? "Yes" : "No",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCropFileUpload = (e, flow) => {
    if (e.target.files?.[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveFile(flow);
    }
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };
  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [activeFile]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onSaveOther = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      editEditable({
        id: instituteId,
        e_data: {
          insAffiliated: state?.insAffiliated ?? "",
          insEditableText_one: state?.insEditableText_one ?? "",
          insEditableText_two: state?.insEditableText_two ?? "",
          affliatedLogo: state?.affliatedLogo ?? "",
          authority: state?.authority ?? "",
          authority_signature: state?.authority_signature ?? "",
          autority_stamp_profile: state?.autority_stamp_profile ?? "",
          leave_certificate_selection: state?.leave_certificate_selection ?? "",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  // console.info("masterData", masterData);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("setting")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div
            className={style.fm_header_container}
            style={{
              gap: "2%",
              alignItems: "flex-end",
            }}
          >
            <CustomSelect
              selectLabel={t("leaving_certificate_form")}
              selectedValue={t("leaving_certificate_form_placeholder")}
              defalutValue={state.certificate_leaving_form_student}
              onClick={(val) =>
                onSelectDropdown(val, "certificate_leaving_form_student")
              }
              options={["Yes", "No"]}
              customStyleContainer={{
                width: "49%",
              }}
              customListStyle={{
                position: "absolute",
                width: "100%",
                zIndex: "1",
              }}
            />
            <CreateButton
              label="save"
              onAction={onSave}
              customStyle={{
                width: "49%",
                margin: "0",
                padding: "0.5rem 1rem",
              }}
            />
          </div>
          <BorderBottom />
          <div>
            <Switch
              {...label}
              checked={state.is_dublicate_bonafide}
              onChange={onDublicateCopyAddOn}
            />
            <span>{t("bonafide_dublicate_copy")}</span>
          </div>
          <BorderBottom />
          <div className={style.fm_header_container}>
            <h6>{t("other_setting")}</h6>
          </div>
          <FormInput
            label={t("aff_with")}
            placeholder="Please enter affiliation body name"
            name={"insAffiliated"}
            value={state.insAffiliated}
            type="text"
            onChange={(e) => onInputChange(e, 50)}
          />
          <StringLength maxLength={50} valueLength={state.insAffiliated} />

          <FormInput
            label={t("accreditation")}
            placeholder="Accredited by NAAC with B++, ISO 9001:2015 Certified"
            name={"insEditableText_one"}
            value={state.insEditableText_one}
            type="text"
            onChange={(e) => onInputChange(e, 200)}
          />
          <StringLength
            maxLength={200}
            valueLength={state.insEditableText_one}
          />
          <FormInput
            label={t("accreditation1")}
            placeholder="Please enter other info like DTE code"
            name={"insEditableText_two"}
            value={state.insEditableText_two}
            type="text"
            onChange={(e) => onInputChange(e, 88)}
          />
          <StringLength
            maxLength={88}
            valueLength={state.insEditableText_two}
          />
          <FormInput
            label={t("competent_authority")}
            placeholder={t("competent_authority_lable")}
            name={"authority"}
            value={state.authority}
            type="text"
            onChange={onInputChange}
          />

          <div
            className={style.fm_header_container}
            style={{
              gap: "2%",
              alignItems: "flex-end",
            }}
          >
            <CustomSelect
              selectLabel={t("certificate_leaving_type")}
              selectedValue={t("certificate_leaving_type_placeholder")}
              defalutValue={state.leave_certificate_selection}
              onClick={(val) =>
                onSelectDropdown(val, "leave_certificate_selection")
              }
              options={["Standard", "SPCE", "PVG College"]}
              customListStyle={{
                position: "absolute",
                width: "100%",
                zIndex: "1",
              }}
              customStyleContainer={{
                width: "49%",
              }}
            />

            <FormFile
              label={t("authority_signature")}
              name={"authority_signature_lable"}
              value={
                state.authority_signature
                  ? {
                      key: state.authority_signature,
                    }
                  : ""
              }
              onChange={(e) => onCropFileUpload(e, "authority_signature")}
              customFormFileLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormFileContainer={{
                width: "49%",
              }}
            />
          </div>
          <div
            className={style.fm_header_container}
            style={{
              gap: "2%",
              alignItems: "flex-end",
            }}
          >
            <FormFile
              label={t("affiliated_logo")}
              name={"affiliated_logo"}
              value={
                state.affliatedLogo
                  ? {
                      key: state.affliatedLogo,
                    }
                  : ""
              }
              type={"text"}
              onChange={(e) => onCropFileUpload(e, "affliatedLogo")}
              customFormFileLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormFileContainer={{
                width: "49%",
              }}
            />

            <FormFile
              label={t("authority_stamp_profile")}
              name={"authority_stamp_profile"}
              value={
                state.autority_stamp_profile
                  ? {
                      key: state.autority_stamp_profile,
                    }
                  : ""
              }
              type={"text"}
              onChange={(e) => onCropFileUpload(e, "autority_stamp_profile")}
              customFormFileLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormFileContainer={{
                width: "49%",
              }}
            />
          </div>
          <CreateButton label="save" onAction={onSaveOther} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}

      <Dialog open={openCrop}>
        <CustomCrop
          setOpenCrop={setOpenCrop}
          fileUrl={fileUrl}
          first={1}
          second={1}
          onCrop={onCrop}
        />
      </Dialog>
    </>
  );
};

export default CertificateSetting;
