import dayjs from "dayjs";
import style from "../../../../InstituteHostel/InstituteHostel.module.css";
import { useTranslation } from "react-i18next";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";

const CertificateLogsCard = ({ d_log }) => {
  const { t } = useTranslation();

  return (
    <>
      <div
        className={style.hostel_warden_card}
        style={{
          paddingBlock: "1rem",
        }}
      >
        <img
          alt="not found"
          src="/images/member_tab/student/student-certificate-icon.svg"
          style={{
            borderRadius: "0",
          }}
        />
        <div className={style.student_cerfificate_container}>
          <div className={style.warden_card_text}>
            <h6>
              {t("name")} {" : "}
              {d_log?.student_name ?? ""}
            </h6>
            <p>
              {t("certificate_type")} {" : "}
              {d_log?.certificate_type === "leaving"
                ? "Leaving"
                : d_log?.certificate_type === "transfer"
                ? "Transfer"
                : d_log?.certificate_type === "BONAFIED"
                ? "Bonafide"
                : d_log?.certificate_type}
            </p>

            {["Bonafide", "BONAFIED"]?.includes(
              d_log?.certificate_type
            ) ? null : (
              <p>
                {t("certificate_copy")} {" : "}
                {d_log?.certificate_issue_type === "Original"
                  ? t("original_copy")
                  : t("dublicate_copy")}
              </p>
            )}
            {d_log?.issue_by_staff ? (
              <p>
                {t("issue_by")} {" : "}{" "}
                {`${d_log?.issue_by_staff?.staffFirstName ?? ""} ${
                  d_log?.issue_by_staff?.staffMiddleName ?? ""
                } ${d_log?.issue_by_staff?.staffLastName ?? ""}`}
              </p>
            ) : (
              <p>
                {t("issue_by")} {" : "} NIL
              </p>
            )}
          </div>

          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <div
              className={style.warden_card_text}
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "flex-end",
              }}
            >
              {d_log?.created_at ? (
                <>
                  <p>
                    {t("issued_on")} {" : "}
                    {dayjs(d_log?.created_at).format("DD/MM/YYYY")}
                    {", "}
                  </p>
                  <p>{localTimeFormat(d_log?.created_at, "LT")}</p>
                </>
              ) : null}
            </div>
            {d_log?.certificate_attachment && (
              <a
                href={`${pdfCdnUrl}/${d_log?.certificate_attachment}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <img
                  src="/images/member_tab/student/student-certificate-download-icon.svg"
                  alt="receipt icon"
                  style={{
                    height: "4.5rem",
                  }}
                />
              </a>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default CertificateLogsCard;
