import { useState } from "react";
import style from "../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import { useCertificateLogsExport } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const CertificateLogsExport = ({ onClose, instituteId }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();
  const [state, setState] = useState({
    from: "",
    to: "",
    certificate_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");

  const [certificateLogsExport] = useCertificateLogsExport();

  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };

  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      certificate_type: val,
    }));
  };

  const onLogsExport = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && instituteId) {
      setDisabled((pre) => !pre);
      certificateLogsExport({
        id: instituteId,
        bodyParameter: {
          ...state,
          certificate_type:
            state.certificate_type === "Bonafide"
              ? "BONAFIED"
              : state.certificate_type === "Leaving"
              ? "leaving"
              : state.certificate_type === "Transfer"
              ? "transfer"
              : state.certificate_type === "LC"
              ? "MLC"
              : state.certificate_type === "MC"
              ? "MTC"
              : state.certificate_type === "All"
              ? "ALL"
              : state.certificate_type,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          navigate(`/q/${params.username}/member/certificate?a=export`, {
            state: getQuery.state,
          });
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("certificate_history_export")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <CustomSelect
              selectLabel={t("certificate_type")}
              selectedValue={t("please_select_certificate_type")}
              options={["All", "Bonafide", "Leaving", "Transfer", "LC", "MC"]}
              onClick={onSelectType}
              defalutValue={state.certificate_type}
            />

            <FormCalendarInput
              label={t("from")}
              placeholder={t("please_select_from_date")}
              name={"from"}
              value={state.from ? slashDateFormat(state.from) : ""}
              onChange={(e) => onTransactionDate(e, "from")}
            />
            <FormCalendarInput
              label={t("to_only")}
              placeholder={t("please_select_to_date")}
              name={"to"}
              value={state.to ? slashDateFormat(state.to) : ""}
              onChange={(e) => onTransactionDate(e, "to")}
            />

            <CreateButton label="export" onAction={onLogsExport} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default CertificateLogsExport;
