import React from "react";
import style from "../PromoteDepartment/PromoteDepartment.module.css";
import CertificateAuthorityTab from "./CertificateAuthorityTab";
import AllStudent from "../FinanceManager/AllStudent/AllStudent";
import { useSearchParams } from "react-router-dom";
import { useOneInstituteDashboard } from "../../../../hooks/dashboard/dashboard-api";
import StudentMessageHistory from "../FinanceManager/AllStudent/StudentMessageHistory";
import CertificateFund from "./CertificateFund/CertificateFund";
// import CertificateRequest from "./CertificateRequest/CertificateRequest";
// import CertificateIssued from "./CertificateRequest/CertificateIssued";
import CertificateCharges from "./CertificateCharges/CertificateCharges";
import CertificateExport from "../../../../optimized/UserStaffMember/CertificateAuthority/Export/CertificateExport";
// import CertificateLogs from "../../../../optimized/UserStaffMember/CertificateAuthority/CertificateLogs/CertificateLogs";
import CertificateSetting from "../../../../optimized/UserStaffMember/CertificateAuthority/CertificateSetting/CertificateSetting";
import CertificateRequestConfig from "../../../../optimized/UserStaffMember/CertificateAuthority/CertificateRequest/CertificateRequestConfig";
const CertificateAuthority = ({ instituteId, carryParentState }) => {
  const [searchParams] = useSearchParams();
  const { oneInstituteDashboard, oneInstituteDashboardRefetch } =
    useOneInstituteDashboard({
      id: instituteId,
      skip: !instituteId,
    });

  React.useEffect(() => {
    if (instituteId) {
      oneInstituteDashboardRefetch();
    }
  }, [instituteId, oneInstituteDashboardRefetch]);

  // console.info("carryParentState", carryParentState);
  return (
    <div className={style.departmenthead}>
      {searchParams.get("a") === "student-message" ? (
        <StudentMessageHistory sid={instituteId} />
      ) : searchParams.get("a") === "export" ? (
        <CertificateExport instituteId={instituteId} />
      ) : //  searchParams.get("a") === "logs" ? (
      //   <CertificateLogs instituteId={instituteId} />
      // ) :
      searchParams.get("a") === "setting" ? (
        <CertificateSetting
          instituteId={instituteId}
          masterData={oneInstituteDashboard?.institute}
        />
      ) : searchParams.get("a") === "request" ? (
        <CertificateRequestConfig instituteId={instituteId} />
      ) : (
        <div className={style.demaprtment_list}>
          {!searchParams.get("a") ? (
            <CertificateAuthorityTab carryParentState={carryParentState} />
          ) : searchParams.get("a") === "students" ? (
            <AllStudent
              instituteId={instituteId}
              viewId={carryParentState?.onClickValue?._id}
              is_view="CERTIFICATE_AUTHORITY"
              studentAlias={
                oneInstituteDashboard?.institute?.alias_pronounciation
                  ?.student_alias
              }
              headId={carryParentState?.sid}
              carryParentState={{
                ...carryParentState,
                staffId: carryParentState?.sid,
              }}
            />
          ) : searchParams.get("a") === "fund" ? (
            <CertificateFund
              instituteId={instituteId}
              issuedCOunt={
                oneInstituteDashboard?.institute?.certificate_issued_count ?? 0
              }
              onRefetch={oneInstituteDashboardRefetch}
            />
          ) : searchParams.get("a") === "charges" ? (
            <CertificateCharges
              instituteId={instituteId}
              fundCharges={
                oneInstituteDashboard?.institute?.certificate_fund_charges
              }
              onRefetch={oneInstituteDashboardRefetch}
            />
          ) : //  searchParams.get("a") === "request" ? (
          //   <CertificateRequest instituteId={instituteId} />
          // ) :
          // searchParams.get("a") === "issued" ? (
          //   <CertificateIssued instituteId={instituteId} />
          // ) :
          null}
        </div>
      )}
    </div>
  );
};

export default CertificateAuthority;
