import { useTranslation } from "react-i18next";
import { useStudentLeaving } from "../../../../hooks/institute/institute-staff-student-api";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useState } from "react";
import dayjs from "dayjs";
import style from "../../../../InstituteHostel/InstituteHostel.module.css";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import CreateButton from "../../../Ui/Button/CreateButton";
const CertificateRequestCard = ({ certificate, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [disabled, setDisabled] = useState(false);
  const navigate = useNavigate();
  const params = useParams();
  const [studentLeaving] = useStudentLeaving();

  const onIssued = () => {
    if (certificate?.certificate_type === "Leaving") {
      setDisabled((pre) => !pre);
      studentLeaving({
        id: instituteId,
        gr: certificate?.student?.studentGRNO,
        leavingFeedback: {
          number: certificate?.student?.studentGRNO,
          studentCertificateNo: "",
          leaving_date: "",
          bookNo: "",
          studentUidaiNumber: "",
          studentPreviousSchool: "",
          studentLeavingBehaviour: "",
          studentLeavingStudy: "",
          studentLeavingReason: "",
          studentRemark: "",
          instituteJoinDate: "",
          instituteLeavingDate: "",
          leaving_degree: "",
          leaving_since_date: "",
          leaving_course_duration: "",
          elective_subject_one: "",
          elective_subject_second: "",
          leaving_project_work: "",
          leaving_guide_name: "",
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          navigate(`/q/${params.username}/student/certificate/form`, {
            state: {
              student: res.data.student,
              download: res.data.download,
              instituteId: instituteId,
              is_leaving: "leaving",
              isViewBy: "REQUEST_TAB",
              certificateId: certificate?._id,
              staffId: getQuery.state?.sid,
            },
          });
        })
        .catch({});
    } else if (certificate?.certificate_type === "Transfer") {
      setDisabled((pre) => !pre);
      studentLeaving({
        id: instituteId,
        gr: certificate?.student?.studentGRNO,
        leavingFeedback: {
          number: certificate?.student?.studentGRNO,
          studentCertificateNo: "",
          leaving_date: "",
          bookNo: "",
          studentUidaiNumber: "",
          studentPreviousSchool: "",
          studentLeavingBehaviour: "",
          studentLeavingStudy: "",
          studentLeavingReason: "",
          studentRemark: "",
          instituteJoinDate: "",
          instituteLeavingDate: "",
          leaving_degree: "",
          leaving_since_date: "",
          leaving_course_duration: "",
          elective_subject_one: "",
          elective_subject_second: "",
          leaving_project_work: "",
          leaving_guide_name: "",
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);

          navigate(`/q/${params.username}/student/certificate/form`, {
            state: {
              student: res.data.student,
              download: res.data.download,
              instituteId: instituteId,
              is_leaving: "transfer",
              isViewBy: "REQUEST_TAB",
              certificateId: certificate?._id,
              staffId: getQuery.state?.sid,
            },
          });
        })
        .catch({});
    } else if (certificate?.certificate_type === "Migration") {
    } else {
      navigate(`/q/${params.username}/student/certificate/form`, {
        state: {
          studentId: certificate?.student?._id,
          staffId: getQuery.state?.sid,
          is_bonafide: true,
          isViewBy: "REQUEST_TAB",
          certificateId: certificate?._id,
        },
      });
    }
  };

  return (
    <>
      <div
        className={style.hostel_warden_card}
        style={{
          paddingBlock: "1rem",
        }}
      >
        <img
          alt="not found"
          src="/images/member_tab/student/student-certificate-icon.svg"
          style={{
            borderRadius: "0",
          }}
        />
        <div className={style.student_cerfificate_container}>
          <div className={style.warden_card_text}>
            <h6>
              {t("certificate_type")} {" : "}
              {certificate?.certificate_type ?? ""}
            </h6>
            <p>
              {t("name")} {" : "}{" "}
              {`${certificate?.student?.studentFirstName ?? ""} ${
                certificate?.student?.studentMiddleName ?? ""
              } ${certificate?.student?.studentLastName ?? ""}`}
            </p>
            <p>
              {t("reason")} {" : "} {certificate?.query_content ?? ""}
            </p>
            {certificate?.certificate_type === "Bonafide" ? null : (
              <p>
                {t("certificate_copy")} {" : "}
                {certificate?.is_original
                  ? t("original_copy")
                  : t("dublicate_copy")}
              </p>
            )}

            <p>
              {t("raised_on")} {" : "}
              {dayjs(certificate?.created_at).format("DD/MM/YYYY")}
              {", "}
              {localTimeFormat(certificate?.created_at, "LT")}
            </p>
            {certificate?.fee_receipt?.fee_payment_mode ? (
              <p>
                {t("invoice_no")} {" : "}
                {certificate?.fee_receipt?.invoice_count ?? "N/A"}
                {", "}
                {t("payment_mode")} {" : "}
                {certificate?.fee_receipt?.fee_payment_mode ?? "N/A"}
              </p>
            ) : null}
          </div>
          <CreateButton
            label="issue_certificate"
            onAction={onIssued}
            customStyle={{
              margin: "0",
              padding: "0.3rem 1rem",
            }}
          />
        </div>
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default CertificateRequestCard;
