import CertificateLogsCard from "./CertificateLogsCard";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useCertificateHistoryLogs } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import CertificateLogsExport from "./CertificateLogsExport";
const CertificateLogs = ({ instituteId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { certificateHistoryLogs, certificateHistoryLogsLoading } =
    useCertificateHistoryLogs({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (certificateHistoryLogs?.list?.length) {
      let obj = nextPage(certificateHistoryLogs?.list?.length);
      setState(() => obj);
    }
  }, [certificateHistoryLogs?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !certificateHistoryLogsLoading &&
      certificateHistoryLogs?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
        <CreateButton
          label="export"
          onAction={onToggle}
          customStyle={{
            margin: "0",
            padding: "0.3rem 1rem",
          }}
        />
      </div>

      <BorderBottom />

      {certificateHistoryLogs?.list?.map((d_log, index) =>
        certificateHistoryLogs?.list?.length === index + 1 ? (
          <div key={d_log?._id} ref={ref}>
            <CertificateLogsCard d_log={d_log} />
          </div>
        ) : (
          <CertificateLogsCard d_log={d_log} key={d_log?._id} />
        )
      )}

      {certificateHistoryLogsLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        certificateHistoryLogs?.list?.length === 0 && (
          <EmptyMessage
            title="No item found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        certificateHistoryLogs?.list?.length === 0 && (
          <EmptyMessage
            title="No item."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      )}

      {add && (
        <CertificateLogsExport onClose={onToggle} instituteId={instituteId} />
      )}
    </>
  );
};

export default CertificateLogs;
